import { isNumber } from '../../utils';

export default {
	props: {
		src: String,
		mode: String,
		lazyLoad: Boolean,
		fadeShow: {
			type: Boolean,
			default: true
		},
		webp: Boolean,
		showMenuByLongpress: Boolean,
		size: {
			type: [String, Number],
			default: '100%'
		},
		round: Boolean
	},

	data() {
		return {
			isError: null
		};
	},

	computed: {
		isRound() {
			return this.round ? 'cl-image--round' : '';
		},
		size2() {
			return isNumber(this.size) ? this.size + 'rpx' : this.size;
		}
	},

	methods: {
		handleError(e) {
			this.isError = true;
			this.$emit('error', e);
		},

		handleLoad(e) {
			this.isError = false;
			this.$emit('load', e);
		}
	}
};
